<template>
  <div>
    <van-cell
      style="margin-top:10px;border-bottom: 1px solid #e4e4e4;"
      title="费用项目"
      is-link
      @click="show_lifeCost = true"
      :value="myLabel"
    />
    <van-cell
      title="收费方式"
      is-link
      style="border-bottom: 1px solid #e4e4e4;"
      @click="show_lifeCostPay = true"
      :value="myMode"
    />
    <div class="unit">
      <van-field
        v-model="myPrice"
        style="width:90%"
        label="单价"
      />
      <div style="width:10%;padding-right:16px;">元/月</div>
    </div>
    <van-button
      class="btn"
      @click="config"
    >确定</van-button>
    <!-- 选择-生活费用 -->
    <van-popup
      v-model="show_lifeCost"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="lifeCostAll"
        @confirm="onConfirmCostType"
        @cancel="show_lifeCost = false"
      />
    </van-popup>
    <!-- 选择-生活费用 -->
    <van-popup
      v-model="show_lifeCostPay"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="modes"
        @confirm="onConfirmCostPayType"
        @cancel="show_lifeCostPay = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { Toast } from 'vant';
export default {
    data() {
        return {
            myLabel: null,
            myValue: null,
            myMode: null,
            myPrice: null,
            show_lifeCost: false,
            show_lifeCostPay: false,
            lifeCostAll: [],
            choosedLifeCostAll: [],
            lifeUints: { 1: '元/度', 3: '元/吨', 6: '元/月' },
            modes: ['抄表收费', '固定收费']
        };
    },
    created() {
        this.$store.commit('setHeader', '添加费用');
        this.myMode = this.modes[0];
        this.getType();
    },
    methods: {
        // 长租---生活费用
        getType() {
            GetSystemDictDataByType({ dictType: 'business_life_cost' }).done((res) => {
                this.lifeCostOldAll = res.data;
                this.lifeCostAll = res.data.map((item) => item.dictLabel);
                this.myLabel = this.lifeCostOldAll[0].dictLabel;
                this.myValue = this.lifeCostOldAll[0].dictValue;
            });
        },
        // 选择-生活费用
        onConfirmCostType(e) {
            this.myLabel = e;
            this.myValue = this.lifeCostOldAll.find((item) => item.dictLabel == e).dictValue;
            this.show_lifeCost = false;
        },
        // 选择-收费方式
        onConfirmCostPayType(e) {
            this.myMode = e;
            this.show_lifeCostPay = false;
        },
        // 确定
        config() {
            if (!this.myPrice) {
                Toast('请输入单价');
                return;
            }
            let arr = [];
            let choosed_price = {
                label: this.myLabel,
                value: this.myValue,
                mode: this.myMode,
                price: this.myPrice
            };
            arr.push(choosed_price);
            localStorage.setItem('set_price', JSON.stringify(arr));
            localStorage.setItem('set_price_scaleType', this.$route.query.scaleType);
            setTimeout('window.history.go(-1)', 900);
        }
    }
};
</script>
<style scoped>
/deep/ .van-cell__title {
    text-align: left;
}
/deep/ .van-cell__value {
    color: #000;
}
/deep/ .van-field__label {
    color: #000;
}
.unit {
    display: flex;
    background: #fff;
    font-size: 14px;
    align-items: center;
    padding-right: 16px;
}
.btn {
    width: 100%;
    background: #fe7d40;
    color: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}
</style>



